/**
 * Local Performance Reporter
 * Real-time performance monitoring for local development
 */

const fs = require('fs');
const path = require('path');

class LocalPerformanceReporter {
  constructor(globalConfig, options = {}) {
    this.globalConfig = globalConfig;
    this.options = {
      outputFile: 'test-results/local-performance.json',
      includeTimings: true,
      thresholds: {
        crisis: 3000,     // Crisis tests must be < 3s
        clinical: 5000,   // Clinical tests must be < 5s
        unit: 1000,       // Unit tests must be < 1s
        integration: 10000 // Integration tests must be < 10s
      },
      ...options
    };
    
    this.testResults = [];
    this.performanceIssues = [];
    this.startTime = Date.now();
  }

  onRunStart() {
    console.log('üîÑ Starting local performance monitoring...\n');
    this.startTime = Date.now();
  }

  onTestResult(test, testResult) {
    if (!this.options.includeTimings) return;

    const testPath = test.path;
    const testCategory = this.categorizeTest(testPath);
    const threshold = this.options.thresholds[testCategory] || 5000;

    testResult.testResults.forEach(result => {
      const duration = result.duration || 0;
      const isSlowTest = duration > threshold;
      const isCrisisTest = this.isCrisisTest(result.fullName);

      const testData = {
        testName: result.fullName,
        testPath: testPath.replace(this.globalConfig.rootDir, ''),
        duration,
        category: testCategory,
        threshold,
        isSlowTest,
        isCrisisTest,
        status: result.status,
        timestamp: new Date().toISOString()
      };

      this.testResults.push(testData);

      // Track performance issues for immediate feedback
      if (isSlowTest) {
        const issue = {
          ...testData,
          severity: isCrisisTest ? 'CRITICAL' : 'WARNING',
          message: isCrisisTest 
            ? `üö® CRISIS TEST PERFORMANCE VIOLATION: ${duration}ms > ${threshold}ms`
            : `‚ö†Ô∏è  SLOW TEST: ${duration}ms > ${threshold}ms`
        };
        
        this.performanceIssues.push(issue);
        
        // Immediate console output for development
        if (isCrisisTest) {
          console.error(`\n${issue.message}`);
          console.error(`   Test: ${result.fullName}`);
          console.error(`   File: ${testData.testPath}\n`);
        } else {
          console.warn(`\n${issue.message}`);
          console.warn(`   Test: ${result.fullName}\n`);
        }
      }

      // Real-time performance feedback for crisis tests
      if (isCrisisTest && duration < threshold) {
        console.log(`‚úÖ Crisis test performance OK: ${duration}ms < ${threshold}ms`);
      }
    });
  }

  onRunComplete() {
    const totalDuration = Date.now() - this.startTime;
    
    this.generatePerformanceReport(totalDuration);
    this.saveResults();
    this.printSummary(totalDuration);
  }

  categorizeTest(testPath) {
    if (testPath.includes('crisis')) return 'crisis';
    if (testPath.includes('clinical')) return 'clinical';
    if (testPath.includes('integration')) return 'integration';
    if (testPath.includes('performance')) return 'performance';
    if (testPath.includes('e2e')) return 'integration';
    return 'unit';
  }

  isCrisisTest(testName) {
    return /crisis|emergency|988|intervention/i.test(testName);
  }

  generatePerformanceReport(totalDuration) {
    const categories = {};
    const slowTests = [];
    const crisisTests = [];

    this.testResults.forEach(test => {
      // Categorize by type
      if (!categories[test.category]) {
        categories[test.category] = {
          count: 0,
          totalDuration: 0,
          slowCount: 0,
          averageDuration: 0
        };
      }

      const cat = categories[test.category];
      cat.count++;
      cat.totalDuration += test.duration;
      if (test.isSlowTest) cat.slowCount++;

      // Track slow tests
      if (test.isSlowTest) {
        slowTests.push(test);
      }

      // Track crisis tests
      if (test.isCrisisTest) {
        crisisTests.push(test);
      }
    });

    // Calculate averages
    Object.keys(categories).forEach(cat => {
      categories[cat].averageDuration = 
        categories[cat].totalDuration / categories[cat].count;
    });

    this.performanceReport = {
      timestamp: new Date().toISOString(),
      totalDuration,
      totalTests: this.testResults.length,
      categories,
      slowTests: slowTests.sort((a, b) => b.duration - a.duration),
      crisisTests,
      performanceIssues: this.performanceIssues,
      recommendations: this.generateRecommendations(categories, slowTests, crisisTests)
    };
  }

  generateRecommendations(categories, slowTests, crisisTests) {
    const recommendations = [];

    // Crisis test recommendations
    const slowCrisisTests = crisisTests.filter(t => t.isSlowTest);
    if (slowCrisisTests.length > 0) {
      recommendations.push({
        type: 'CRITICAL',
        category: 'crisis',
        message: `${slowCrisisTests.length} crisis tests exceed 3s threshold`,
        action: 'Optimize crisis detection algorithms and reduce async operations',
        tests: slowCrisisTests.map(t => t.testName)
      });
    }

    // Unit test recommendations
    if (categories.unit && categories.unit.averageDuration > 500) {
      recommendations.push({
        type: 'WARNING',
        category: 'unit',
        message: `Unit tests average ${Math.round(categories.unit.averageDuration)}ms (target: <500ms)`,
        action: 'Review mocking strategies and reduce external dependencies'
      });
    }

    // Integration test recommendations
    if (categories.integration && categories.integration.slowCount > categories.integration.count * 0.3) {
      recommendations.push({
        type: 'INFO',
        category: 'integration',
        message: `${Math.round(categories.integration.slowCount/categories.integration.count*100)}% of integration tests are slow`,
        action: 'Consider parallel execution or test optimization'
      });
    }

    return recommendations;
  }

  saveResults() {
    const outputDir = path.dirname(this.options.outputFile);
    if (!fs.existsSync(outputDir)) {
      fs.mkdirSync(outputDir, { recursive: true });
    }

    // Save detailed results
    fs.writeFileSync(
      this.options.outputFile,
      JSON.stringify(this.performanceReport, null, 2)
    );

    // Save simplified summary for quick reference
    const summary = {
      timestamp: this.performanceReport.timestamp,
      totalDuration: this.performanceReport.totalDuration,
      totalTests: this.performanceReport.totalTests,
      slowTestCount: this.performanceReport.slowTests.length,
      crisisTestCount: this.performanceReport.crisisTests.length,
      criticalIssues: this.performanceIssues.filter(i => i.severity === 'CRITICAL').length,
      categories: Object.keys(this.performanceReport.categories).reduce((acc, cat) => {
        acc[cat] = {
          count: this.performanceReport.categories[cat].count,
          averageDuration: Math.round(this.performanceReport.categories[cat].averageDuration)
        };
        return acc;
      }, {})
    };

    fs.writeFileSync(
      this.options.outputFile.replace('.json', '-summary.json'),
      JSON.stringify(summary, null, 2)
    );
  }

  printSummary(totalDuration) {
    console.log('\nüìä Local Performance Test Summary');
    console.log('====================================');
    console.log(`Total Duration: ${totalDuration}ms`);
    console.log(`Total Tests: ${this.testResults.length}`);
    
    if (this.performanceIssues.length > 0) {
      const critical = this.performanceIssues.filter(i => i.severity === 'CRITICAL');
      const warnings = this.performanceIssues.filter(i => i.severity === 'WARNING');
      
      console.log(`\n‚ö†Ô∏è  Performance Issues: ${this.performanceIssues.length}`);
      if (critical.length > 0) {
        console.log(`   üö® Critical: ${critical.length} (Crisis test violations)`);
      }
      if (warnings.length > 0) {
        console.log(`   ‚ö†Ô∏è  Warnings: ${warnings.length} (Slow tests)`);
      }
    } else {
      console.log('\n‚úÖ All tests within performance thresholds');
    }

    // Category breakdown
    if (this.performanceReport.categories) {
      console.log('\nCategory Performance:');
      Object.entries(this.performanceReport.categories).forEach(([cat, data]) => {
        const avg = Math.round(data.averageDuration);
        const slowPct = Math.round((data.slowCount / data.count) * 100);
        console.log(`   ${cat}: ${data.count} tests, ${avg}ms avg, ${slowPct}% slow`);
      });
    }

    // Recommendations
    if (this.performanceReport.recommendations?.length > 0) {
      console.log('\nüí° Recommendations:');
      this.performanceReport.recommendations.forEach(rec => {
        console.log(`   ${rec.type === 'CRITICAL' ? 'üö®' : rec.type === 'WARNING' ? '‚ö†Ô∏è' : '‚ÑπÔ∏è'} ${rec.message}`);
        console.log(`      Action: ${rec.action}`);
      });
    }

    console.log(`\nüìÑ Detailed report: ${this.options.outputFile}`);
    console.log('====================================\n');
  }
}

module.exports = LocalPerformanceReporter;